import Vue from 'vue'
import Router from 'vue-router'
import list from '@/components/list'
import add from '@/components/add'

Vue.use(Router)
export default new Router({
    routes: [
        {
            path: '/',
            name: 'list',
            component: list
        },
        {
            path: '/add',
            name: 'add',
            component: add
        },
    ]
})



