<template>
    <div>
        <div class="buttons">
            <a href="#/add" style="display: inline-block"><el-button type="primary">添加商品<i class="el-icon-plus el-icon--right"></i></el-button></a>
            <el-button type="danger" v-on:click="removeall">批量删除<i class="el-icon-delete el-icon--right"></i>
            </el-button>
        </div>
        <div class="buttons">
            <el-row :gutter="20">
                <!--普通输入内容-->
                <el-col :span="6">
                    <el-input v-model="filter.keyword" placeholder="请输入内容"></el-input>
                </el-col>
                <!--级联选择器-->
                <el-col :span="4">
                    <el-cascader
                            :options="options"
                            v-model="filter.category"
                    ></el-cascader>
                </el-col>
                <el-col :span="2">
                    <div class="grid-content bg-purple">
                        <el-select size="mini" v-model="filter.price_area" placeholder="请选择价格区间">
                            <el-option
                                    v-for="item in price_areas"
                                    :label="item.label"
                                    :value="item.value"
                            >
                            </el-option>
                        </el-select>
                    </div>
                </el-col>
                <el-col :span="6">
                    <el-button type="primary" icon="el-icon-search" @click="search">搜索</el-button>
                    <el-button type="primary" icon="el-icon-refresh" @click="refresh">重置</el-button>
                </el-col>
            </el-row>
        </div>
        <div class="buttons">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="page"
                    :page-sizes="[pageSize,100, 200, 300, 400]"
                    :page-size="pageSize"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total">
            </el-pagination>
        </div>
        <div class="list">
            <el-table
                    :data="products"
                    style="width: 100%"
                    tooltip-effect="dark"
                    @selection-change="handleSelectionChange">
                <el-table-column
                        type="selection"
                        width="55">
                </el-table-column>
                <el-table-column type="expand">
                    <template slot-scope="props">
                        <el-form label-position="left" inline class="demo-table-expand">
                            <el-form-item label="商品成本">
                                <span>{{ props.row.const_price }}</span>
                            </el-form-item>
                            <el-form-item label="店铺ID">
                                <span>{{ props.row.shop_id }}</span>
                            </el-form-item>
                            <el-form-item label="商品 ID">
                                <span>{{ props.row.id }}</span>
                            </el-form-item>
                            <el-form-item label="店铺 ID">
                                <span>{{ props.row.shopId }}</span>
                            </el-form-item>
                            <el-form-item label="商品分类">
                                <span>{{ props.row.category }}</span>
                            </el-form-item>

                            <el-form-item label="商品描述">
                                <span>{{ props.row.short_description }}</span>
                            </el-form-item>
                        </el-form>
                    </template>
                </el-table-column>
                <el-table-column
                        label="商品 ID"
                        prop="id">
                </el-table-column>
                <el-table-column
                        label="商品名称"
                        prop="name">
                </el-table-column>
                <el-table-column
                        label="商品图片">
                    <template slot-scope="scope">
                        <img :src="scope.row.image" alt="" style="width: 50px;height: 50px;">
                    </template>
                </el-table-column>
                <el-table-column
                        label="描述"
                        prop="short_description">
                </el-table-column>
                <el-table-column
                        label="价格"
                        prop="price">
                </el-table-column>
                <el-table-column
                        label="库存"
                        prop="price">
                </el-table-column>
                <el-table-column
                        label="操作">
                    <template slot-scope="scope">
                        <el-button type="primary" size="mini" icon="el-icon-edit" circle></el-button>
                        <el-button type="danger" size="mini" icon="el-icon-delete" circle
                                   v-on:click="remove(scope.row.id)"></el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>

    </div>
</template>
<script>
    import axios from 'axios'

    export default {
        name: 'list',
        data() {
            return {
                // 选择方法开关
                switch:false,
                // list总条数
                products: [],
                // 页数
                page:1,
                // 显示条数
                pageSize:50,
                // 总数
                total:null,
                //批量删除id
                removeall_id:[],
                // 普通输入框内容
                name:'',
                // 级联选择器
                options:[],
                price_areas: [{label: '全部', value: 0}, {label: '0-20', value: 1}, {label: '20-40', value: 2}, {
                    label: '40以上',
                    value: 3
                }],
                // 选框
                filter:{
                    // 搜索关键字
                    keyword: '',
                    // 当前选中的分类
                    category: [],
                    // 选中的价格区间
                    price_area: 0,

                }

            }
        },
        methods: {
            // 刷新
            refresh(){
                this.switch = false;
                this.fetchData();
                this.filter ={
                    // 搜索关键字
                    keyword: '',
                        // 当前选中的分类
                        category: [],
                        // 选中的价格区间
                        price_area: 0,

                }
            },
            // 搜索请求
            search(){
                this.switch = true
                this.page = 1
                this.searchData();
            },
            // 单个删除方法
            remove(id) {
                this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {

                    axios({
                        url:'/api/ic/product',
                        method:'delete',
                        data:{
                            id:id
                        }
                    }).then(res => {
                        this.$message({
                            type: 'success',
                            message: '删除成功!'
                        });
                        if(this.switch){
                            this.searchData()
                        }else{
                            this.fetchData();
                        }
                    })
                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            // 复选框中的方法
            handleSelectionChange(val) {
                this.removeall_id = val.map(v => v.id);
            },
            //批量删除
            removeall() {
                this.$confirm('此操作不可恢复, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    if(this.removeall_id.length){
                        axios({
                            url:'/api/ic/product',
                            method:'delete',
                            data:this.removeall_id
                        }).then(res => {
                            this.$message({
                                type: 'success',
                                message: '删除成功!'
                            });
                            if(this.switch){
                                this.searchData()
                            }else{
                                this.fetchData();
                            }
                        })
                    }else{
                        this.$message({
                            type: 'waring',
                            message: '请选中你要删除的商品!'
                        });
                    }

                }).catch(() => {
                    this.$message({
                        type: 'info',
                        message: '已取消删除'
                    });
                });
            },
            // 分页当中的方法
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                this.page = val
                if(this.switch){
                    this.searchData();
                }else{
                    this.fetchData();
                }
            },
            // 发送ajax函数
            fetchData:function () {
                axios({
                    url: '/api/ic/product',
                    params:{
                        page:this.page,
                        pageSize:this.pageSize
                    }
                }).then(res => {

                    let a =  res.data.searchdata.map(v => {
                         v.price = v.price/100
                        return v;
                    })
                    this.products = a
                    this.total = res.data.total

                })
            },
            // 搜索发送ajax函数
            searchData:function () {

                axios({
                    url:'/api/ic/product',
                    params:{
                        page:this.page,
                        pageSize:this.pageSize,
                        keyword:this.filter.keyword,
                        category:this.filter.category.join(','),
                        price_area:this.filter.price_area
                    }
                }).then(res => {

                    let a =  res.data.searchdata.map(v => {
                        v.price = v.price/100
                        return v;
                    })
                    this.products = a;
                    this.total = res.data.total;
                })
            }
        },
        mounted: function () {
            this.fetchData();
            axios({
                url:'/api/ic/product',
                method:'get'
            }).then(res => {
                let arr = res.data.searchdata;
                let fn = function (id) {
                    let cate = [];
                    arr.forEach(v => {
                        if(v.parent_id == id){
                            let o = {
                                label:v.name,
                                value:v.id,
                            }
                            let children = fn(v.id)
                            if(children.length){
                                o.children = children
                            }
                            cate.push(o);
                        }
                    })
                    return cate;
                }
                this.options = fn(0)
            })
        }
    }
</script>
<style>
    .demo-table-expand {
        font-size: 0;
    }

    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }

    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }

    /*检索样式*/
    .el-row {
        margin-bottom: 20px;

    &:last-child {
        margin-bottom: 0;
    }

    }
    .el-col {
        border-radius: 4px;
    }

    .bg-purple-dark {
        background: #99a9bf;
    }

    .bg-purple {
        background: #d3dce6;
    }

    .bg-purple-light {
        background: #e5e9f2;
    }

    .grid-content {
        border-radius: 4px;
        min-height: 36px;
    }

    .row-bg {
        padding: 10px 0;
        background-color: #f9fafc;
    }
</style>

<style>
    .buttons{
        margin: 10px 0;
        padding:0 10px;
    }
    .list{
        padding:0 10px;
    }
</style>


